#padding {
  padding: 30px;
}

div.row {
  width: 100%;
  justify-content: center;
  margin: 0;
}

div.container {
  margin-bottom: 60px;
}

.card {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  padding: 16px;
  text-align: center;
  background-color: #f1f1f1;
}

.iconClass {
  padding-left: 5px;
  padding-right: 5px;
}

.fa-github-square {
  color: #999999;
}

.fa-github-square:hover,
.fa-github-square:active {
  color: #24292e;
}

.fa-linkedin {
  color: #999999;
}

.fa-linkedin:hover,
.fa-linkedin:active {
  color: #0077b5;
}

.fa-facebook-square {
  color: #999999;
}

.fa-facebook-square:hover,
.fa-facebook-square:active {
  color: #1778f2;
}

.fa-envelope-square {
  color: #999999;
}

.fa-envelope-square:hover,
.fa-envelope-square:active {
  color: #b23121;
}

.fa-phone-square {
  color: #999999;
}

.fa-phone-square:hover,
.fa-phone-square:active {
  color: #309b30;
}
